import MyNav from "../components/MyNav";
import Carousel from "../components/Carousel";
import Product from "../components/Product";
import QuoteCarosuel from "../components/Quotes";
import Form from "../components/Form";
import Footer from "../components/Footer";
import about1 from "./../img/about1.jpg";
import about2 from "./../img/about2.jpg";
import { ApiContext } from "../App";
import { useContext, useEffect } from "react";
import { useLocation, useNavigate } from "react-router-dom";

function Home() {
  const location = useLocation();
  const navigate = useNavigate();
  const { dataProducts } = useContext(ApiContext);
  let isTakeData = false;
  let topArr;

  if (dataProducts) {
    isTakeData = true;
    topArr = dataProducts.filter((val, idx) => {
      return val.top === true;
    });
  }

  useEffect(() => {
    if (location.pathname.endsWith("/")) {
      const newPathname = location.pathname.slice(0, -1);
      navigate(newPathname, { replace: true });
    }
  }, [location.pathname, navigate]);

  return (
    <>
      <MyNav></MyNav>
      <Carousel></Carousel>

      <section className="container mt-6">
        <div className="row">
          <div className="col-lg">
            <img className="w-100 img-ab" src={about1} alt="about" />
          </div>
          <div className="col-lg my-3">
            <div className="h-100 d-flex align-items-center">
              <div>
                <h3 className="mb-4">Thit k, sn xut m hnh thy sinh</h3>
                <p className="mb-4">
                  Chng ti chuyn thit k v sn xut nhng m hnh thy sinh
                  cht lng cao, sc nt n tng chi tit. Vi s kt hp ti
                  nng v s sng to ca chng ti, chng ti to ra nhng cnh
                  quan nc sng tuyt vi, to khng gian sng t nhin cho c
                  cnh v mang n tri nghim tuyt vi cho khch hng ca bn.
                </p>
                <button className="btn btn-primary btn-lg">Lin h</button>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section className="container mt-5">
        <div className="row">
          <div className="col-lg my-3">
            <div className="h-100 d-flex align-items-center">
              <div>
                <h3 className="mb-4">Dch v bo tr h c</h3>
                <p className="mb-4">
                  Chng ti cung cp dch v bo tr h c chuyn nghip cho ca
                  hng bn c cnh ca bn. Vi i ng chuyn gia giu kinh
                  nghim v hiu bit su sc v h sinh thi nc, chng ti
                  cam kt m bo h c ca bn lun trong tnh trng hon ho.
                </p>
                <button className="btn btn-primary btn-lg">Lin h</button>
              </div>
            </div>
          </div>
          <div className="col-lg">
            <img className="w-100 img-ab" src={about2} alt="about" />
          </div>
        </div>
      </section>

      <section className="bg-dark text-light">
        <div className="container py-7 mt-6">
          <div className="row">
            <div className="col-sm mb-3">
              <p className="text-center mt-3">
                <i className="fa-solid fa-fish fa-2xl"></i>
              </p>
              <h4 className="text-center mt-5">Ngun gc r rng</h4>
              <p className="text-center mt-4">
                Chng ti cung cp c t nhng ngun cung cp ng tin cy v c
                ngun gc r rng.
              </p>
            </div>
            <div className="col-sm mb-3">
              <p className="text-center mt-3">
                <i className="fa-solid fa-notes-medical fa-2xl"></i>
              </p>
              <h4 className="text-center mt-5">Ngun gc r rng</h4>
              <p className="text-center mt-4">
                Chng ti cung cp c t nhng ngun cung cp ng tin cy v c
                ngun gc r rng.
              </p>
            </div>
            <div className="col-sm mb-3">
              <p className="text-center mt-3">
                <i className="fa-solid fa-gear fa-2xl"></i>
              </p>
              <h4 className="text-center mt-5">Ngun gc r rng</h4>
              <p className="text-center mt-4">
                Chng ti cung cp c t nhng ngun cung cp ng tin cy v c
                ngun gc r rng.
              </p>
            </div>
          </div>
        </div>
      </section>

      <section className="container py-5 mt-4">
        <h2 className="text-center mb-5">CC GING C BN CHY NHT</h2>
        <div className="row mb-4">
          {isTakeData &&
            topArr.map((val, idx) => (
              <div className="col-lg-3 col-md-4 col-6 mb-4" key={idx}>
                <Product data={val}></Product>
              </div>
            ))}
        </div>
      </section>

      <QuoteCarosuel />
      <Form />
      <Footer />
    </>
  );
}

export default Home;
